
const routes = [
    {
        path:"/",
        component:()=>import("../layout/UserLayout"),
        redirect:"/user/login",
        children:[
            {
                path:"/user/login",
                component:()=>import("../view/user/login")
            },
            {
                path:"/user/register",
                component:()=>import("../view/user/register")
            }
        ]
        
    },
    {
        path:"/main",
        component:()=>import("../layout/MainLayout"),
        redirect:"/main/index",
        children:[
        {
            path:"/main/index",
            component:()=>import("../view/pages/index"),
            redirect:"/main/index/news",
            children:[
                {
                    path:"/main/index/news",
                    component:()=>import("../components/news")
                },
                {
                    path:"/main/index/depDynamic",
                    component:()=>import("../components/depDynamic")
                },
                {
                    path:"/main/index/townshDynamic",
                    component:()=>import("../components/townshDynamic")
                },
                {
                    path:"/main/index/notice",
                    component:()=>import("../components/notice")
                },
            ]
        }
        ]
    }
   
]

export default routes;


