import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
    state:{
        username:""
    },
    mutations:{
        getUsername(state,name){
            state.username = name
        }
    },
    actions:{
        getUsername({commit},name){
            commit("getUsername",name)
        }
    }
});
export default store;


